@import url(https://fonts.googleapis.com/css?family=Maven+Pro:400,500,700,900);
@import url(https://fonts.googleapis.com/css?family=Signika:700);
/* Fonts */

body {
  font-family: 'Maven Pro', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    color: #423D2D;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body, .App {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-size: 1.2rem;
  line-height: 1.666;
}
.link {
  color: inherit;
  position: relative;
}
.link:hover {
  color: #FDC44A;
  text-decoration: none;
}
.link_underline::after {
  content: "";
  width: 100%;
  height: 0.4rem;
  background-color: #FDC44A;
  position: absolute;
  left: 0;
  bottom: -0.4rem;
}
.btn-google, .btn-apple {
    display: inline-block;
    width: 43vw;
    max-width: 9.5rem;
    border-radius: 6px;
}

.btn-google {
    margin-right: 0.6rem;
}

.btn-apple {
    pointer-events: none;
    cursor: default;
}
.hero {
  position: relative;
  padding-top: 33%;
  height: 50rem;
  margin-bottom: 4rem;
  overflow: hidden;
  background: linear-gradient(-135deg, #FFB751 0%, #FDC149 29%, #FFD652 82%, #FFD900 100%); }
  .hero::after {
    content: "";
    position: absolute;
    bottom: -1px;
    width: 100%;
    height: 100%;
    background: url(/static/media/white-gray-shape.0d1306a6.svg) no-repeat 0 100%;
    background-size: 100% 13vw; }
  .hero__title {
    font-family: 'Signika', 'Maven Pro', sans-serif;
    font-size: 2.7rem; }
  .hero__body {
    position: relative;
    z-index: 33; }
    .hero__body p {
      margin-bottom: 1.5rem;
      max-width: 90%;
      font-weight: 500; }
  .hero__media {
    position: absolute;
    bottom: -5rem;
    width: 30rem;
    right: -9rem; }
  @media screen and (orientation: landscape) {
    .hero {
      padding-top: 33vh; } }
  @media screen and (min-width: 900px) {
    .hero {
      padding-top: 0;
      margin-bottom: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 95vh;
      max-height: 56rem; }
      .hero__title {
        font-size: 3.7rem;
        max-width: 50%; }
      .hero__media {
        bottom: 0;
        width: 37rem;
        right: -12rem;
        height: auto; }
      .hero__body p {
        width: 50%; } }
  @media screen and (min-width: 995px) {
    .hero > .container {
      position: relative; }
    .hero__media {
      bottom: -14rem;
      right: -14rem;
      width: 62vw; } }
  @media screen and (min-width: 1200px) {
    .hero__media {
      bottom: -16rem;
      right: -16rem;
      width: 60vw;
      max-width: 51rem; } }

.features {
  padding-bottom: 4rem;
  background: url(/static/media/bg-shape.64f54031.svg) no-repeat bottom;
  background-size: 100% 35%; }
  @media screen and (min-width: 992px) {
    .features {
      background-size: 100% 46%; } }

.feature {
  display: flex;
  flex-direction: column; }
  .feature:not(:last-child) {
    margin-bottom: 10rem; }
  .feature__title {
    display: inline-block;
    position: relative;
    align-self: baseline;
    font-size: 1.7rem;
    font-weight: 600;
    margin-bottom: 1.4rem;
    letter-spacing: 1px;
    text-transform: capitalize; }
    .feature__title::after {
      content: "";
      position: absolute;
      top: -0.5rem;
      left: 0;
      width: 15%;
      height: 0.5rem;
      background-color: #FFCC05; }
  .feature__media {
    margin-right: auto;
    margin-left: auto;
    position: relative;
    left: 0; }
  .feature_horizontal {
    text-align: center; }
    .feature_horizontal p {
      text-align: left; }
  .feature_horizontal .feature__media {
    width: 100%;
    margin-bottom: 4rem; }
    .feature_horizontal .feature__media img {
      -webkit-filter: drop-shadow(0px 9px 30px rgba(193, 181, 199, 0.53));
              filter: drop-shadow(0px 9px 30px rgba(193, 181, 199, 0.53)); }
  .feature_vertical .feature__media {
    height: 100vw;
    margin-top: 4rem; }
    .feature_vertical .feature__media img {
      width: auto;
      height: 100%;
      -webkit-filter: drop-shadow(-9px 0px 20px rgba(193, 181, 199, 0.53));
              filter: drop-shadow(-9px 0px 20px rgba(193, 181, 199, 0.53)); }
  @media screen and (min-width: 575px) {
    .feature_vertical .feature__media {
      height: 35rem; } }
  @media screen and (min-width: 767px) {
    .feature_horizontal .feature__media {
      width: 35rem; } }
  @media screen and (min-width: 800px) {
    .feature_vertical .feature__media, .feature_vertical .feature__body {
      margin-top: 0;
      flex: 0 0 50%; }
    .feature_vertical .feature__body {
      margin-top: 3rem; }
    .feature_vertical {
      flex-direction: row-reverse; } }
  @media screen and (min-width: 992px) {
    .feature {
      top: -5rem;
      position: relative; }
      .feature_horizontal .feature__body {
        margin: auto;
        width: 70%; }
      .feature_vertical .feature__body {
        display: flex;
        flex-direction: column;
        justify-content: flex-end; }
      .feature_vertical .feature__media {
        flex: 0 0 30%; } }
  @media screen and (min-width: 1200px) {
    .feature_vertical .feature__body {
      justify-content: center; } }

.display-linebreak {
  white-space: pre-line; }

.jobs {
  padding-bottom: 4rem; }
  .jobs__hero {
    background: url(/static/media/jobs.117a4e29.jpg) no-repeat;
    background-size: cover;
    height: 35rem; }
    .jobs__hero > .container {
      position: relative;
      z-index: 99; }
    .jobs__hero::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
      opacity: 0.7;
      background: linear-gradient(-135deg, #FFB751 0%, #FDC149 29%, #FFD652 82%, #FFD900 100%); }
    .jobs__hero .hero__title {
      border-left: 8px solid white;
      padding: 0 0 0 1rem; }
  .jobs__section > ul {
    padding: 0;
    list-style: none; }

.job {
  margin-top: 5rem; }
  .job__head {
    margin-bottom: 1rem; }
  .job:not(:last-child) {
    border-bottom: solid rgba(239, 239, 239, 0.9) 2px;
    padding-bottom: 3rem; }
  .job__requirements {
    margin-bottom: 2rem; }
    .job__requirements li {
      margin-bottom: 0.4rem; }

.default-page {
  padding: 10rem 0;
  width: 75%;
  margin: auto; }

.notfound {
  height: 60vh;
  min-height: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .notfound__body {
    text-align: center; }
    .notfound__body h1 {
      font-size: 10rem; }
    .notfound__body p {
      font-size: 1.5rem; }

.navbar {
    z-index: 100;
}
.navbar .nav-link {
    color: inherit;
    font-weight: 600;
}
.footer {
  background-color: #FDC44A;
  text-align: center;
  padding: 3rem 0 3rem; }
  .footer__cta {
    margin-bottom: 1rem; }
  .footer__logo {
    margin-bottom: 1.5rem; }
  .footer__nav {
    padding: 0;
    justify-content: center; }
    .footer__nav .nav-item {
      text-transform: capitalize; }
      .footer__nav .nav-item:not(:last-child) {
        margin-right: 2rem; }
  .footer__copy {
    margin-top: 1rem;
    font-weight: 600; }
  @media screen and (min-width: 992px) {
    .footer {
      padding: 5rem 0 1.5rem; }
      .footer__cta {
        margin-bottom: 4rem; } }

